<template>
    <form class="questions" @submit.prevent="handleSubmit">
        <h1 class="margin-bottom--three">Infomación de contacto</h1>

        <div class="row col-sm-6">
            <h5 class="margin-bottom--zero">
                Número de teléfono
                <span class="text--gray"> (opcional)</span>
            </h5>

            <div class="row">
                <div class="col-xs-3">
                    <Input
                        v-model="form.phone"
                        placeholder="+01"
                        class="phone-field"
                        type="tel"
                    />
                </div>
                <div class="col-xs-9">
                    <Input
                        v-model="form.phone"
                        class="phone-field"
                        type="tel"
                    />
                </div>
            </div>
        </div>

        <div class="row">
            <h5 class="margin-top--two margin-bottom--zero col-xs-12">
                Dirección
                <span class="text--gray"> (opcional)</span>
            </h5>
            <div class="col-xs-12 col-sm-6">
                <Select
                    v-model="form.address.country"
                    label="país"
                    :options="countries"
                ></Select>
            </div>
            <div class="col-xs-12 col-sm-6">
                <Input v-model="form.address.city" label="cuidad" />
            </div>
        </div>

        <Input v-model="form.address.line" label="dirección" textarea />

        <div class="row">
            <div class="col-xs-12 col-sm-6">
                <Input
                    v-model="form.address.zip_code"
                    label="código postal"
                    type="number"
                />
            </div>
            <div class="col-xs-12 col-sm-6">
                <Input v-model="form.address.state" label="estado/provincia" />
            </div>
        </div>

        <div class="margin-top--two">
            <Button :href="route('questions', 1)" flat>Atras</Button>
            <Button type="submit" :loading="loading">Siguiente</Button>
        </div>
    </form>
</template>

<script>
export default {
    props: {
        old: Object,
        countries: { type: Array, required: true },
        errors: ErrorBag,
        loading: Boolean,
    },

    data: () => ({
        form: {
            phone: '',
            address: {
                country: null,
                city: '',
                line: '',
                zip_code: '',
                state: '',
            },
        },
    }),

    methods: {
        async handleSubmit() {
            if (this.loading) return

            this.errors.clear()
            this.loading = true

            try {
                const data = this.form
                await this.$http.post('questions/2', data)
                window.location.href = route('questions', 3)
            } catch (error) {
                this.loading = false
                console.log(error.response)
            }
        },
    },
}
</script>
